button {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-items: center;
    background: none;
    border: none;
    outline: none;
    cursor: pointer;
}

.paginationWrapper {
    display: flex;
    align-items: center;
    height: 26px;
    justify-content: flex-end;
    gap: 12px;
    margin-top: 24px;
    margin-bottom: 20px;
    text-align: center;
    color: #808080;
}

.paginationDivItems {
    width: 21px;
    padding: 2px;
    border-radius: 3px;
    cursor: pointer;
    text-align: center;
}

.paginationDivItems a {
    text-decoration: none;
    width: 20px;
    height: 20px;
    color: #808080;
}

.paginationDivItems:hover {
    background-color: #599dff;
    color: white;
}

.paginationDivItems_Active {
    background-color: #0064EB;
    color: white;
}

.paginationItems {
    font-size: 16px;
}

.paginationIcon {
    font-size: 16px;
    font-weight: 900;
    color: #808080;
    padding-bottom: 3px;
}

.paginationIcon:hover {
    opacity: 0.8;
    color: #0064EB;
}

.paginationDivItems a:active {
    color: red;
}

.paginationInfo {
    font-size: 14px;
    font-weight: 400;
    color: #808080;
    margin-right: 16px;
}

@media (max-width: 576px) {
    .paginationWrapper {
        margin-right: -50px;
    }

    .paginationDivItems {
        width: 26px;
        border-radius: 3px;
    }

    .paginationInfo {
        font-size: 12px;
        margin-right: 0;
    }

}